blockquote {
	font-family: Georgia, serif;
	position: relative;
	margin: 0.3em;
	padding: 0.5em 2em 0.5em 3em;
}

blockquote:before {
	color: green;
	font-family: Georgia, serif;
	position: absolute;
	font-size: calc(0.3 * 33vw);
	line-height: 1;
	top: 0;
	left: 0;
	content: "\201C";
}

blockquote:after {
	color: green;
	font-family: Georgia, serif;
	position: absolute;
	float: right;
	font-size: calc(0.3 * 33vw);
	line-height: 1;
	right: 0;
	bottom: -0.5em;
	content: "\201D";
}

blockquote footer {
	margin-top: 1.5em;
	padding: 0 2em 0 0;
	text-align: right;
}

blockquote cite:before {
	content: "\2013";
}